* {
  margin: 0;
  padding: 0;
}

.timeline-container {
  max-width: 90%;
  margin: 0 auto;
  margin-top: 2em;
  height: 5em;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
}

.timeline-container > span {
  width: 25px;
  height: 100%;
  border-right: .1px solid grey;
  margin: 0;
  padding: 0;
}

.timeline-container span:first-child {
  border-top-left-radius: 14px;
  border-bottom-left-radius: 14px;
}
.timeline-container span:last-child {
  border-right: 0px;
  border-top-right-radius: 14px;
  border-bottom-right-radius: 14px;
}

/* Hours */

.hourly-visual {
  max-width: 90%;
  margin: 0 auto;
}

.timeline-div {
  display: -ms-flexbox;
  display: flex;
  margin: 0;
  padding: 0;
}
.timeline-display-hours {
  /* background: yellow; */
  margin: 0;
  padding: 0;
  margin: 0 auto;
  height: 3em;
  width: 90%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;

}

.timeline-display-hours-span {
  width: 100%;
}
.timeline-display-hours-span > span {
  max-width: .53%;
  min-width: .53%;
}
.timeline-display-hours-span:last-child {
  border-right: 0px;
}
span.timeline-display-hours-span {
  border-right: .1px solid grey;
}



